import { Component, OnInit } from '@angular/core';
import { CompanyServiceService } from 'src/app/service/company-service.service';
import { company } from 'src/app/models/company';

@Component({
  selector: 'app-get-company-details',
  templateUrl: './get-company-details.component.html',
  styleUrls: ['./get-company-details.component.css']
})
export class GetCompanyDetailsComponent implements OnInit {

  constructor(private myService:CompanyServiceService) { }
   company:company
  ngOnInit() {
    this.myService.getCompanyDetails().subscribe(
      comp=>{
        this.company = comp;
      },err=>{
        alert("oops! "+ err.error)
      }
    )
  }

}
