import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Coupon } from '../models/coupon';
import { company } from '../models/company';
import { category } from '../models/category';



@Injectable({
  providedIn: 'root'
})
export class CompanyServiceService {

  constructor(private httpClient: HttpClient) { }

  public getAllCoupons(): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("/Company/getAllCoupons/"+localStorage.myToken)
  }
  public addCoupon(coupon:Coupon){
    return this.httpClient.post<Coupon>("/Company/addCoupon/"+localStorage.myToken,coupon);
  }
  public updateCoupon(coupon:Coupon){
    return this.httpClient.put<Coupon>("/Company/updateCoupon/"+ localStorage.myToken,coupon);
  }
  public deleteCoupon(id:number){
    return this.httpClient.delete("/Company/deleteCoupon/"+ localStorage.myToken+ "/" + id, {responseType:'text'});
  }
  public getOneCoupon(id:number){
    return this.httpClient.get<Coupon>("/Company/getOneCoupon/"+ localStorage.myToken+"/"+id);
  }
  public getCouponsByMaxPrice(maxPrice:number){
    return this.httpClient.get<Coupon[]>("/Company/getCouponsByMaxPrice/"+ localStorage.myToken+"/"+maxPrice);
  }
  public getCouponsByCategory(category:number){
    return this.httpClient.get<Coupon[]>("/Company/getCouponsByCategory/"+localStorage.myToken+"/"+category);
  }
  public getCompanyDetails():Observable<company>{
    return this.httpClient.get<company>("/Company/getCompanyDetails/"+ localStorage.myToken);
  }

}
