import { Component, OnInit } from '@angular/core';
import { CompanyServiceService } from 'src/app/service/company-service.service';
import { category } from 'src/app/models/category';
import { company } from 'src/app/models/company';
import { Coupon } from 'src/app/models/coupon';

@Component({
  selector: 'app-get-coupons-by-category',
  templateUrl: './get-coupons-by-category.component.html',
  styleUrls: ['./get-coupons-by-category.component.css']
})
export class GetCouponsByCategoryComponent implements OnInit {

  constructor(private myService:CompanyServiceService) { }
category:category
coupons:Coupon[]
options: string[] = Object.keys(category)
  ngOnInit() {
    this.options = this.options.slice(this.options.length/2)
  }
  public getCouponsByCategory(){
    this.myService.getCouponsByCategory(this.category).subscribe(coups=>{
      this.coupons = coups;
    }, err=>{
      alert("oops! "+ err.error)
    })
  }
}
