import { Component, OnInit } from '@angular/core';
import { CompanyServiceService } from 'src/app/service/company-service.service';
import { Coupon } from 'src/app/models/coupon';

@Component({
  selector: 'app-get-one-coupon',
  templateUrl: './get-one-coupon.component.html',
  styleUrls: ['./get-one-coupon.component.css']
})
export class GetOneCouponComponent implements OnInit {

  constructor(private myService:CompanyServiceService) { }
  id:number
  coupon:Coupon
  ngOnInit() {
  }
  public getOneCoupon(){
    this.myService.getOneCoupon(this.id).subscribe(coup=>{
      this.coupon = coup;
    }, err=>{
      alert("oops! "+ err.error)
    })
  }
}
