import { Component, OnInit } from '@angular/core';
import { CompanyServiceService } from 'src/app/service/company-service.service';
import { Coupon } from 'src/app/models/coupon';

@Component({
  selector: 'app-get-coupons-by-max-price',
  templateUrl: './get-coupons-by-max-price.component.html',
  styleUrls: ['./get-coupons-by-max-price.component.css']
})
export class GetCouponsByMaxPriceComponent implements OnInit {

  constructor(private myService:CompanyServiceService) { }
  maxPrice:number
  coupons:Coupon[]
  ngOnInit() {
  }
  public getCouponsByMaxPrice(){
    this.myService.getCouponsByMaxPrice(this.maxPrice).subscribe(coups=>{
      this.coupons = coups;
    }, err=>{
      alert("oops! "+ err.error)
    })
  }
}
