import { Component, OnInit } from '@angular/core';
import { CompanyServiceService } from 'src/app/service/company-service.service';
import { category } from 'src/app/models/category';
import { company } from 'src/app/models/company';
import { Coupon } from 'src/app/models/coupon';

@Component({
  selector: 'app-add-coupon',
  templateUrl: './add-coupon.component.html',
  styleUrls: ['./add-coupon.component.css']
})
export class AddCouponComponent implements OnInit {

  constructor(private myService:CompanyServiceService) { }
  
  company:company;
  title:string;
  category:category;
  description:string;
  image:string;
  startDate:Date;
  endDate:Date;
  amount:number;
  price:number;
  options: string[] = Object.keys(category)


  ngOnInit() {
    this.options = this.options.slice(this.options.length/2)
  }
  public addCoupon(){
    this.myService.getCompanyDetails().subscribe(comp=>{
      this.company = comp;
      this.myService.addCoupon(new Coupon(0,this.company,this.title,this.category,this.description,this.image,
        this.startDate,this.endDate,this.amount,this.price)).subscribe(
          ()=>{
            alert("coupon added successfully")
          },err=>{
            alert("oops! "+ err.error)
          }
        )
        
    });

  }
  }

