import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyServiceService } from 'src/app/service/company-service.service';

@Component({
  selector: 'app-get-all-coupons',
  templateUrl: './get-all-coupons.component.html',
  styleUrls: ['./get-all-coupons.component.css']
})
export class GetAllCouponsComponent implements OnInit {

  constructor(private myService:CompanyServiceService) { }
  coupons:Coupon[]
  
  ngOnInit() {
    this.myService.getAllCoupons().subscribe(
      coupons =>{
        this.coupons = coupons
        console.log(coupons)
      }, err=>{
        alert("oops! "+ err.error)
      }
    )
    
    
    }

    public deleteCoupon(id:number){
      this.myService.deleteCoupon(id).subscribe(
        coup=>{
          alert("coupon deleted succssefully !")
          for(let i=0; i< this.coupons.length; i++){
            if(this.coupons[i].id == id){
              this.coupons.splice(i, 1);
              break;
            }
          }
        }, err => {
          alert("oops! "+ err.error)
        }
      )
    }
  
  }
 
  
  
  


